"use strict";
function rnd(n) {
    return Math.floor(Math.random() * (n + 1));
}
exports.rnd = rnd;
function frnd(range) {
    return Math.random() * range;
}
exports.frnd = frnd;
function pick(choices) {
    return choices[Math.floor(Math.random() * choices.length)];
}
exports.pick = pick;



